import React,{useState,useEffect} from 'react'
import './SignUp.css'
import {Link,Redirect} from 'react-router-dom'
import Logo from './SoftArcLogo.jpg'

function SignUp() {
    const [isDesktop, setDesktop] = useState(window.innerWidth <= 800);
    const updateMedia = () => {
        setDesktop(window.innerWidth <= 800);
    };

    useEffect(() => {
        window.addEventListener("resize", updateMedia);
        return () => window.removeEventListener("resize", updateMedia);
      });

    return (
        <div className="components">
    
            <div className="left-component">
                {!isDesktop ? (
                <div className="left-content">
                <h1 className="head-text">Hello Friend,</h1>
                <p className="head-text-2">If you already have an account, 
                just sign in. We've missed you.</p>
                <Link to="/login"><button className="btn-2">Sign In</button></Link>
                </div>
                ):
                (
                <div className="left-content">
                <p className="head-text-2">If you already have an account, 
                just <Link className="link" to="/login">Sign in </Link> .We've missed you.</p>
                </div>

                )}

            </div>

            <div className="right-component">
            <div className="head">
                <img src={Logo}/>
            </div>
                <div className="body">
                    <h1 className="head-text-3">Create Free Account</h1>
                     <p className="head-text-4">Sign up using social networks</p>
                     <div className="social-media-buttons">
                     <a href="#" class="fa fa-facebook"></a>
                     <a href="#" class="fa fa-google"></a>
                     </div>
                     <div className="wrapper">
                     <div className="border">OR</div>
                     </div>   
                     <div className="form-elements">
                         <form>
                             <input className="input" type="text" name="FullName" placeholder="FullName"/>
                             <input className="input" type="text" name="Email" placeholder="Email"/>
                             <input className="input" type="password" name="pass" placeholder="Password"/>
                             <input className="input" type="password" name="pass-confirm" placeholder="Confirm Password"/>
                             <input className="btn-1" type="submit" value="Sign Up" name="submit-btn" />
                         </form>

                     </div>

                </div>
            

            </div>

            

        </div>
    )
}

export default SignUp
